function isStringEmpty(str) {
  return str == null || String(str).trim().length === 0;
}

export default {
  data() {
    return {
      rules: {
        required: (value) =>
          (value != null && String(value).trim().length > 0) || "Obrigatório",
        greaterThanZero: (required) => (value) => {
          if (isStringEmpty(value)) {
            if (required === true) {
              return "Deve ser um número maior que zero";
            }
            return true;
          }
          return (
            /^[1-9]\d*$/.test(value) || "Deve ser um número maior que zero"
          );
        },
        greaterOrEqualThanZero: (value) => {
          if (value == null) {
            return "Deve ser um número maior ou igual a zero";
          }
          const num = parseInt(value, 10);
          return (
            (Number.isInteger(num) && num >= 0) ||
            "Deve ser um número maior ou igual a zero"
          );
        },
        lessThanOrEqual: (limit, required) => (value) => {
          if (isStringEmpty(value)) {
            if (required === true) {
              return "Deve ser um número menor ou igual a " + limit;
            }
            return true;
          }
          const num = parseInt(value, 10);
          return (
            (Number.isInteger(num) && num <= limit) ||
            "Deve ser um número menor ou igual a " + limit
          );
        },
      },
    };
  },

  methods: {
    resetValidation(refName) {
      if (refName != null && this.$refs[refName] != null) {
        this.$refs[refName].resetValidation();
      } else if (this.$refs.form != null) {
        this.$refs.form.resetValidation();
      }
    },
    validate(refName) {
      if (refName != null && this.$refs[refName] != null) {
        return this.$refs[refName].validate();
      } else if (this.$refs.form != null) {
        return this.$refs.form.validate();
      }
    },
  },
};
