<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="horarios"
      :item-class="itemRowBackground"
      multi-sort
      dense
      :footer-props="{
        'items-per-page-options': [5, 10, 25, { text: 'Todos', value: -1 }],
        'show-current-page': true,
        'show-items-per-page': true,
        'show-first-last-page': true,
        'items-per-page-text': 'Linhas por página:',
        'items-per-page-all': 'Todos',
        'page-text': '{0}-{1} de {2}',
      }"
      :loading="$store.getters.loading"
      loading-text="Carregando horários"
      no-data-text="Nenhum horário encontrado"
    >
      <template v-slot:top>
        <v-toolbar flat dense>
          <v-toolbar-title>{{ toolbarTitle }}</v-toolbar-title>
          <v-spacer />

          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn text tile medium v-on="on" @click="load" color="primary">
                <v-icon>mdi-refresh </v-icon>
              </v-btn>
            </template>
            <span>Recarregar</span>
          </v-tooltip>

          <v-menu offset-y>
            <template v-slot:activator="{ on }">
              <v-btn v-on="on" icon>
                <v-icon>mdi-dots-vertical</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                v-for="menuOption in menuOptions"
                :key="menuOption.value"
                @click="menuOption.action()"
              >
                <v-list-item-title>{{ menuOption.text }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>

          <v-btn
            color="primary"
            dark
            class="mr-2"
            @click="openSavedFiltersDialog"
          >
            Filtrar
          </v-btn>

          <v-tooltip top v-if="showSpinner">
            <template v-slot:activator="{ on }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                @click="cancelGenerationDialog = true"
                v-on="on"
              >
                <template>
                  <v-progress-circular
                    :indeterminate="bestProgress === 0"
                    :value="bestProgress.toString()"
                    :size="20"
                    :width="3"
                  >
                  </v-progress-circular>
                </template>
              </v-btn>
            </template>
            <span>Clique para cancelar</span>
          </v-tooltip>

          <v-btn color="primary" dark @click="generate" v-else> Gerar </v-btn>
        </v-toolbar>
        <div class="mb-15">
          <v-row
            v-for="(filter, index) in filters"
            :key="index"
            style="height: 60px; padding: 20px"
          >
            <v-col cols="5">
              <v-select
                v-model="filter.id_metrica_qualidade"
                :items="metrics"
                item-text="desc_metrica"
                item-value="id_metrica_qualidade"
                hide-details
                label="Métrica"
              />
            </v-col>
            <v-col cols="3">
              <v-select
                v-model="filter.id_professor"
                :items="professores"
                item-text="name"
                item-value="id"
                hide-details
                label="Professor"
                :disabled="!isTeacherFilterEnabled(filter.id_metrica_qualidade)"
                clearable
              />
            </v-col>
            <v-col cols="2">
              <v-select
                v-model="filter.regra"
                :items="opcoesRegras"
                hide-details
                label="Regra"
                item-text="text"
                item-value="id"
              />
            </v-col>
            <v-col cols="2">
              <v-text-field hide-detais label="Valor" v-model="filter.valor">
                <template slot="append-outer">
                  <v-icon @click="deleteFilter(index)">mdi-delete</v-icon>
                  <v-icon v-if="index === filters.length - 1" @click="addFilter"
                    >mdi-plus</v-icon
                  >
                </template>
              </v-text-field>
            </v-col>
          </v-row>
        </div>
      </template>

      <!-- eslint-disable-next-line -->
      <template v-slot:item.data_hora_geracao="props">
        {{ new Date(props.item.data_hora_geracao).toLocaleString("pt-BR") }}
      </template>

      <!-- eslint-disable-next-line -->
      <template v-slot:item.icon="{ item }">
        <v-icon v-if="item.hard_constraints_cost > 0" small color="red">
          mdi-alert
        </v-icon>
      </template>

      <!-- eslint-disable-next-line -->
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="viewSchedule(item.id_horario)"
          :color="
            item.id_horario === $store.state.selectedSchedule ? 'primary' : null
          "
        >
          mdi-eye
        </v-icon>
        <v-icon small class="mr-2" @click="deleteSchedule(item)">
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
    <v-dialog v-model="deleteScheduleDialog" max-width="600px">
      <v-card>
        <v-card-title class="text-h5">{{
          deleteScheduleDialogTitle
        }}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="closeDeleteScheduleDialog"
            :disabled="loading"
            >Cancelar</v-btn
          >
          <v-btn
            color="blue darken-1"
            text
            @click="deleteScheduleConfirm"
            :loading="loading"
            >OK</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="cancelGenerationDialog" max-width="700px">
      <v-card>
        <v-card-title class="text-h5"
          >Deseja mesmo cancelar a geração da grade horária?</v-card-title
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="cancelGenerationDialog = false"
            >Não</v-btn
          >
          <v-btn color="blue darken-1" text @click="cancelGenerationConfirm"
            >Sim, parar geração</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="saveFiltersDialog" max-width="700px">
      <v-card>
        <v-card-title class="text-h5">{{
          saveFiltersDialogTitle
        }}</v-card-title>
        <v-card-text>
          <v-container>
            <v-form
              v-if="
                currentFilterGroup != null &&
                !currentFilterGroup.id_filtro_horario
              "
              @submit.prevent
              ref="form"
            >
              <v-row>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="currentFilterGroup.desc_filtro_horario"
                    label="Nome do filtro"
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="cancelSaveFilters"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="saveFilters">Salvar</v-btn>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="savedFiltersDialog" max-width="700px">
      <v-card>
        <v-card-title class="text-h5">Filtros Salvos</v-card-title>
        <v-card-text>
          <v-container>
            <v-data-table
              :items="filterGroups"
              :headers="savedFiltersHeaders"
              hide-default-footer
            >
              <!-- eslint-disable-next-line -->
              <template v-slot:item.actions="{ item }">
                <v-btn
                  color="primary"
                  dark
                  outlined
                  @click="useSavedFilter(item)"
                >
                  Aplicar
                </v-btn>
              </template>
            </v-data-table>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="savedFiltersDialog = false"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="addFilter"
            >Novo filtro</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="headerSelectionDialog" max-width="700px">
      <v-card>
        <v-card-title class="text-h5">Customizar colunas</v-card-title>
        <v-card-text>
          <v-data-table
            :items="availableHeaders"
            :headers="customColumnsHeaders"
            show-select
            v-model="selectedHeadersEdit"
            item-key="value"
            disable-pagination
            dense
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="headerSelectionDialog = false"
            >Cancelar</v-btn
          >
          <v-btn color="blue darken-1" text @click="customizeHeaders"
            >Salvar</v-btn
          >
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import helper from "@/mixins/helper";
import validationMixin from "@/mixins/validation";
export default {
  name: "ListaHorarios",
  props: ["id"],
  mixins: [helper, validationMixin],

  data() {
    return {
      horarios: [],
      workers: [],
      showInvalid: false,
      bestProgress: 0,
      customColumnsHeaders: [{ text: "Nome", value: "text" }],
      savedFiltersHeaders: [
        { text: "Nome", value: "desc_filtro_horario" },
        { value: "actions", align: "end" },
      ],
      availableHeaders: [],
      interval: null,
      deletingInvalid: null,
      deletingScheduleId: null,
      deleteScheduleDialog: false,
      cancelGenerationDialog: false,
      loading: false,
      selectedHeadersEdit: [],
      selectedHeaders: [],
      headerSelectionDialog: false,
      selection: [],
      requestingGenerationTimeout: null,
      cancelingGenerationTimeout: null,
      filters: [],
      metrics: [],
      opcoesRegras: [
        { text: "Exatamente", id: 0 },
        { text: "No mínimo", id: 1 },
        { text: "No máximo", id: 2 },
      ],
      saveFiltersDialog: false,
      currentFilterGroup: null,
      savedFiltersDialog: false,
      filterGroups: null,
    };
  },

  async mounted() {
    this.load();
    await this.loadAvailableMetrics();
    this.loadCustomHeaders();
    this.getStructure();
  },

  computed: {
    showSpinner() {
      return (
        !this.cancelingGenerationTimeout &&
        (this.requestingGenerationTimeout != null || this.workers.length > 0)
      );
    },
    headers() {
      const iconHeader = [{ text: "", value: "icon", sortable: false }];
      const actionHeader = [
        { text: "Ações", value: "actions", sortable: false, width: "90px" },
      ];
      return actionHeader.concat(iconHeader).concat(this.selectedHeaders);
    },
    menuOptions() {
      const options = [
        {
          text: this.showInvalid
            ? "Esconder horários inválidos"
            : "Mostrar horários inválidos",
          key: "show-invalid",
          action: () => (this.showInvalid = !this.showInvalid),
        },
        {
          text: "Excluir horários inválidos",
          key: "delete-invalid",
          action: () => this.deleteSchedules(true),
        },
        {
          text: "Excluir todos horários",
          key: "delete-all",
          action: this.deleteSchedules,
        },
        {
          text: "Editar configurações de geração",
          key: "delete-all",
          action: () => this.$emit("open-dialog"),
        },
        {
          text: "Editar colunas",
          key: "edit-columns",
          action: this.openCustomizeHeaders,
        },
      ];
      if (
        this.filters.length ||
        (this.currentFilterGroup != null &&
          this.currentFilterGroup.id_filtro_horario)
      ) {
        options.push({
          text: "Limpar filtros",
          key: "edit-columns",
          action: this.clearFilters,
        });
      }
      if (this.processFilters().length) {
        options.push({
          text: "Salvar filtros",
          key: "save-filters",
          action: () => {
            this.saveFiltersDialog = true;
          },
        });
      }
      return options;
    },
    deleteScheduleDialogTitle() {
      if (this.deletingInvalid === true) {
        return "Deseja mesmo excluir todos os horários inválidos?";
      }
      if (this.deletingScheduleId != null) {
        return "Deseja mesmo excluir esse horário?";
      }
      return "Deseja mesmo excluir todos os horários?";
    },
    saveFiltersDialogTitle() {
      if (
        this.currentFilterGroup != null &&
        this.currentFilterGroup.id_filtro_horario != null
      ) {
        return `Salvar filtros atuais como ${this.currentFilterGroup.desc_filtro_horario} ?`;
      }
      return "Salvar filtros atuais";
    },
    toolbarTitle() {
      if (
        this.currentFilterGroup &&
        this.currentFilterGroup.desc_filtro_horario
      ) {
        return (
          "Horários Gerados (Filtro " +
          this.currentFilterGroup.desc_filtro_horario +
          ")"
        );
      }
      return "Horários Gerados";
    },
  },

  methods: {
    async load(showLoading = true) {
      try {
        if (showLoading) {
          this.setLoading();
        }
        const resp = await this.$axios.post("/horario", {
          id_configuracao: this.id,
          filters: this.processFilters(),
        });

        const respWorkers = await this.$axios.get("/generation/workers", {
          params: {
            id_configuracao: this.id,
          },
        });
        if (respWorkers.status === 200) {
          if (this.requestingGenerationTimeout != null) {
            clearTimeout(this.requestingGenerationTimeout);
            this.requestingGenerationTimeout = null;
          }
          this.workers = respWorkers.data.workers;
          this.setBestProgress();
          for (const worker of respWorkers.data.workers) {
            let errorMessage = null;
            let warnMessage = null;
            if (
              worker.validationError != null &&
              worker.validationError.length
            ) {
              errorMessage = worker.validationError.trim();
            } else if (worker.status === "error") {
              errorMessage =
                "Ocorreu um erro inesperado durante a geração da grade horária";
            } else if (
              worker.message != null &&
              worker.message.trim().length > 0
            ) {
              warnMessage = worker.message;
            }

            if (errorMessage != null) {
              this.$toast.error(errorMessage, {
                timeout: 5000,
              });
              this.workers = [];
              this.stopInterval();
              this.cancelWorker(worker.id, true);
            }
            if (warnMessage != null) {
              this.$toast.warning(warnMessage, {
                timeout: 5000,
              });
              this.workers = [];
              this.stopInterval();
              this.cancelWorker(worker.id, true);
            }
          }
        }

        this.horarios = resp.data.horarios;
        if (resp.data.horarios != null && resp.data.horarios.length) {
          const currentSchedule = this.getSchedule(
            this.$store.state.selectedSchedule
          );
          const bestSchedule = resp.data.horarios[0];
          if (currentSchedule == null) {
            this.viewSchedule(bestSchedule.id_horario);
          }
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.clearLoading();
      }
    },
    loadCustomHeaders() {
      const savedHeaders = localStorage.getItem("customHeaders");
      if (savedHeaders != null) {
        const parsedHeaders = JSON.parse(savedHeaders);
        this.selectedHeaders = this.availableHeaders.filter((header) =>
          parsedHeaders.includes(header.value)
        );
      } else {
        this.setDefaultHeaders();
      }
    },
    openCustomizeHeaders() {
      this.selectedHeadersEdit = [...this.selectedHeaders];
      this.headerSelectionDialog = true;
    },
    customizeHeaders() {
      this.selectedHeaders = [...this.selectedHeadersEdit];
      this.headerSelectionDialog = false;
      const selectedHeadersJSON = JSON.stringify(
        this.selectedHeadersEdit.map((e) => e.value)
      );
      localStorage.setItem("customHeaders", selectedHeadersJSON);
    },
    getSchedule(scheduleId) {
      return this.horarios.find(
        (schedule) => schedule.id_horario === scheduleId
      );
    },
    viewSchedule(scheduleId) {
      this.$store.dispatch("setSelectedSchedule", scheduleId);
    },
    setBestProgress() {
      if (this.workers == null || this.workers.length === 0) {
        return 0;
      }
      this.bestProgress = Math.max(
        ...Array.from(this.workers, (wk) => wk.progress)
      );
    },
    deleteSchedules(invalid = false) {
      this.deletingInvalid = invalid;
      this.deletingScheduleId = null;
      this.deleteScheduleDialog = true;
    },
    closeDeleteScheduleDialog() {
      this.deletingInvalid = null;
      this.deletingScheduleId = null;
      this.deleteScheduleDialog = false;
    },
    async deleteScheduleConfirm() {
      if (this.deletingScheduleId != null) {
        this.loading = true;
        try {
          await this.$axios.delete("/horario", {
            data: {
              id_configuracao: this.id,
              id_horario: this.deletingScheduleId,
            },
          });
        } catch (e) {
          console.error(e);
        } finally {
          this.loading = false;
        }
      } else {
        this.loading = true;
        try {
          await this.$axios.delete("/horario", {
            data: {
              id_configuracao: this.id,
              invalido: this.deletingInvalid,
            },
          });
        } catch (e) {
          console.error(e);
        } finally {
          this.loading = false;
        }
      }
      this.load();
      this.closeDeleteScheduleDialog();
    },
    async deleteSchedule(item) {
      this.deleteScheduleDialog = true;
      this.deletingScheduleId = item.id_horario;
    },
    startInterval() {
      if (this.interval === null) {
        this.interval = setInterval(() => this.load(false), 5000);
      }
    },
    stopInterval() {
      if (this.interval) {
        clearInterval(this.interval);
        this.interval = null;
      }
    },
    cancelWorker(workerId, supressMessage) {
      this.bestProgress = 0;
      return this.$axios.post("/generation/cancel", {
        id_configuracao: this.id,
        worker_id: workerId,
        suppressMessage: supressMessage,
      });
    },
    itemRowBackground: function (item) {
      return item.hard_constraints_cost > 0 ? "invalid" : null;
    },
    async generate() {
      this.requestingGenerationTimeout = setTimeout(
        () => (this.requestingGenerationTimeout = null),
        5000
      );

      const response = await this.$axios.post("/command/generate/", null, {
        params: { id_configuracao: this.id },
      });

      if (response.status === 200) {
        setTimeout(this.load, 100);
        this.startInterval();
      }
    },
    async cancelGenerationConfirm() {
      this.cancelGenerationDialog = false;
      this.bestProgress = 0;
      this.cancelingGenerationTimeout = setTimeout(
        () => (this.cancelingGenerationTimeout = null),
        5000
      );
      const resp = await this.$axios.post("/generation/cancel", {
        id_configuracao: this.id,
      });
      clearTimeout(this.cancelingGenerationTimeout);
      this.cancelingGenerationTimeout = null;
      if (resp.status === 200) {
        this.load();
        this.stopInterval();
      }
    },
    getCurrentScheduleIndex() {
      const selectedSchedule = this.$store.state.selectedSchedule;
      return this.horarios.findIndex(
        (schedule) => schedule.id_horario === selectedSchedule
      );
    },
    nextSchedule() {
      const currentIndex = this.getCurrentScheduleIndex();
      const nextSchedule = this.horarios[currentIndex + 1];
      if (nextSchedule) {
        this.$store.dispatch("setSelectedSchedule", nextSchedule.id_horario);
      }
    },
    previousSchedule() {
      const currentIndex = this.getCurrentScheduleIndex();
      const previousSchedule = this.horarios[currentIndex - 1];
      if (previousSchedule) {
        this.$store.dispatch(
          "setSelectedSchedule",
          previousSchedule.id_horario
        );
      }
    },
    setDefaultHeaders() {
      const defaultHeaders = [
        "id_horario",
        "hard_constraints_cost",
        "soft_constraints_cost",
      ];
      this.selectedHeaders = this.availableHeaders.filter((header) =>
        defaultHeaders.includes(header.value)
      );
    },
    async loadAvailableMetrics() {
      const resp = await this.$axios.get("/configuracoes_geracao/metrics");
      const defaultMetrics = [
        { text: "Identificador", value: "id_horario" },
        {
          text: "Problemas",
          value: "hard_constraints_cost",
        },
        {
          text: "Detalhes",
          value: "soft_constraints_cost",
        },
        {
          text: "Observações",
          value: "observacao",
        },
      ];
      if (resp.data.metrics) {
        this.metrics = resp.data.metrics.filter(
          (metric) => Number(metric.filtravel) === 1
        );
        this.availableHeaders = defaultMetrics.concat(
          resp.data.metrics.map((metric) => {
            return {
              text: metric.desc_metrica,
              value: metric.chave_xml.toLowerCase(),
            };
          })
        );
      }
    },
    getEmptyFilter() {
      return {
        id_professor: null,
        id_metrica_qualidade: null,
        valor: null,
        regra: 0,
      };
    },
    addFilter() {
      if (
        this.currentFilterGroup != null &&
        this.currentFilterGroup.id_filtro_horario != null
      ) {
        this.clearFilters();
      }

      this.filters.push(this.getEmptyFilter());
      if (this.currentFilterGroup == null) {
        this.currentFilterGroup = {
          desc_filtro_horario: null,
        };
      }
      this.savedFiltersDialog = false;
    },
    deleteFilter(index) {
      this.filters.splice(index, 1);
    },
    isTeacherFilterEnabled(metricId) {
      const metric = this.metrics.find(
        (metric) => metric.id_metrica_qualidade === metricId
      );
      return metric != null && Number(metric.tem_professor) === 1;
    },
    processFilters() {
      return this.filters
        .filter((filter) => {
          if (
            filter.id_metrica_qualidade == null ||
            filter.valor == null ||
            String(filter.valor).trim().length === 0
          ) {
            return false;
          }
          return true;
        })
        .map((filter) => {
          if (!this.isTeacherFilterEnabled(filter.id_metrica_qualidade)) {
            delete filter.id_professor;
          }
          return filter;
        });
    },
    cancelSaveFilters() {
      this.saveFiltersDialog = false;
    },
    async saveFilters() {
      const processedFilters = this.processFilters();
      if (!processedFilters.length) {
        return;
      }

      const filtersJson = JSON.stringify(processedFilters);
      const saveParams = {
        id_configuracao: this.id,
        definicao: filtersJson,
        desc_filtro_horario: this.currentFilterGroup.desc_filtro_horario,
      };
      if (
        this.currentFilterGroup != null &&
        this.currentFilterGroup.id_filtro_horario
      ) {
        saveParams.id_filtro_horario =
          this.currentFilterGroup.id_filtro_horario;
      }

      await this.$axios.post("/filter/save", saveParams);
      await this.loadFilterGroups();
      this.saveFiltersDialog = false;
    },
    async openSavedFiltersDialog() {
      if (this.filterGroups == null) {
        await this.loadFilterGroups();
      }
      if (!this.filterGroups.length) {
        this.addFilter();
      } else {
        this.savedFiltersDialog = true;
      }
    },
    async loadFilterGroups() {
      const resp = await this.$axios.post("filter/get", {
        id_configuracao: this.id,
      });
      this.filterGroups = resp.data.filtros;
    },
    useSavedFilter(item) {
      this.currentFilterGroup = item;
      this.filters = [...item.definicao];
      this.savedFiltersDialog = false;
      this.load(true);
    },
    clearFilters() {
      this.currentFilterGroup = null;
      this.filters = [];
    },
  },
};
</script>
